@import "../../../style/pageStyle.scss";

.podologia {

  width: 95%;
  margin: auto;

  & .podologia--header {
    font-size: 2em;

    & .podologia__header--p::first-letter {
      font-weight: bold;
      padding-left: 10px;
    }

                & .podologia__header--p::grammar-error {
                  text-decoration: underline red;
                  color: red;
                }
                }
}

